import Grid from '../components/Grid';
import logo from '../assets/images/Logo-2.png';
import {Link} from 'react-router-dom';

const footerAboutLinks = [
    {
        display: 'Gii Thiu',
        path: '/about',
    },
    {
        display: 'Lin H',
        path: '/about',
    },
    {
        display: 'Tin Tc',
        path: '/about',
    },
    {
        display: 'H Thng Ca Hng',
        path: '/about',
    },
];

const footerCustomerLinks = [
    {
        display: 'Chnh sch bo hnh',
        path: '/about',
    },
    {
        display: 'Chnh sch i tr',
        path: '/about',
    },
    {
        display: 'Chnh sch hon tin',
        path: '/about',
    },
];

const Footer = () => {
    return (
        <footer className='footer'>
            <div className='container'>
                <Grid col={4} mdCol={2} smCol={1} gap={10}>
                    <div>
                        <div className='footer__title'>Tng i h tr</div>
                        <ul className='footer__content'>
                            <li>
                                Lin h t hng <strong>0987954221</strong>
                            </li>
                            <li>
                                Thc mc n hng <strong>0987954221</strong>
                            </li>
                            <li>
                                Gp  , khiu ni <strong>0987954221</strong>
                            </li>
                        </ul>
                    </div>
                    <div>
                        <div className='footer__title'>V Yolo</div>
                        <ul className='footer__content'>
                            {footerAboutLinks.map((item, index) => {
                                return (
                                    <li key={index}>
                                        <Link to={item.path}>{item.display}</Link>
                                    </li>
                                );
                            })}
                        </ul>
                    </div>
                    <div>
                        <div className='footer__title'>Chm sc khch hng</div>
                        <ul className='footer__content'>
                            {footerCustomerLinks.map((item, index) => {
                                return (
                                    <li key={index}>
                                        <Link to={item.path}>{item.display}</Link>
                                    </li>
                                );
                            })}
                        </ul>
                    </div>
                    <div className='footer__about'>
                        <p>
                            <Link to='/'>
                                <img className='footer__logo' src={logo} alt='' />
                            </Link>
                        </p>
                        <p>
                            Lorem ipsum dolor, sit amet consectetur adipisicing elit. Laudantium vero dicta ipsam fugit, sunt soluta accusamus magnam modi. Ducimus nulla modi quos ex eaque quo laudantium voluptates officia repudiandae ipsum!
                        </p>
                    </div>
                </Grid>
            </div>
        </footer>
    );
};

export default Footer;
